/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import userData from './data-bb-user-http';

const userDataModuleKey = 'data-bb-user-http-ng';

export const userDataKey = 'data-bb-user-http-ng:userData';

export default ng
  .module(userDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(userDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       userData(config),
      ],
    };
  }])
  .name;



